import React from 'react'
import styled from 'styled-components'

import { Facebook } from './facebook'
import { Instagram } from './instagram'
import { Linkedin } from './linkedin'
import { Twitter } from './twitter'
import { Github } from './github'

const profileGitHubURL = 'https://github.com/alessioforte'

export const Footer = () => (
  <Box>
    <Content>
      Created with <span style={{ color: 'darkRed' }}></span> by Alessio Forte
      <Link
        onClick={function() {
          window.open(profileGitHubURL)
        }}
      >
        <Github />
      </Link>
      <br />Copyright  2017
    </Content>
  </Box>
)

const Content = styled.div`
  margin-top: 6px;
  font-size: 12px;
  color: #999;
  letter-spacing: 0.1rem;
  text-align: center;
  line-height: 1.2rem;
`
const Link = styled.div`display: inline-block;`
const Box = styled.div`
  width: 100%;
  height: 100px;
  display: flex;
  justify-content: center;
  background: #1a1a1a;
`



